.bar-graph {
    position: relative;
    width: 100%;
    max-width: 400px;
    height: 170px;
    margin-top: 100px;
    margin-bottom: 100px;
  }
  
  .bars-list {
    display: flex;
    align-items: flex-end;
    column-gap: 20px;
    width: 100%;
    height: 100%;
  }
  
  .bars-line {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #505050;
    opacity: 0.5;
    pointer-events: none;
  }

  /* Bar Graph Horizontal */
